@import url("https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem;
}

html,
body {
  background: #0f0f0f;
  color: #fff;
}

::selection {
  background: #fff;
  color: #000;
}

/* navBar */

.container .navBar {
  position: fixed;
  background: #0f0f0f;
  top: 0;
  width: 100%;
  padding: 2em 2em 2em 2em;
  display: flex;
  justify-content: center;
  text-align: center;
  line-height: 2em;
  text-decoration: none;
  color: #fff;
  font-family: "Space Grotesk", sans-serif;
}

.container .navBar span {
  font-weight: 400;
  color: #979797;
}

.container .navBar .siteLogo {
  font-weight: bolder;
}

.container .navBar .aboutLink {
  text-decoration: none;
  color: #979797;
  font-weight: 500;
}

.container .navBar .aboutLink:hover {
  cursor: pointer;
}

/* socialLinks */

.socialLinks {
  padding: 1em;
  margin: 10em auto 2em auto;
  display: grid;
  gap: 1rem;
}

.card {
  background: #000000;
  height: 8rem;
  padding: 1em;
  display: flex;
  align-items: flex-end;
  font-family: "Space Grotesk", sans-serif;
}

.socialLinks a {
  text-decoration: none;
  font-weight: 500;
  color: #fff;
}

.card:hover {
  cursor: pointer;
}

@media (min-width: 600px) {
  .socialLinks {
    grid-template-columns: repeat(2, 1fr);
  }
}

.profilePictureContainer {
  background: url(/src/images/harrnish-cover-img.jpg) no-repeat 50% 50%;
  background-size: cover;
}

.youtubeContainer {
  background: #ff0000;
}

.instagramContainer {
  border: 2px solid transparent;
  background: transparent;
  color: #ff0069;
  border-image: linear-gradient(
    to bottom right,
    #ffd600 0%,
    #ff7a00 25%,
    #ff0069 50%,
    #d300c5 75%,
    #7638fa 100%
  );
  border-image-slice: 1;
}

.linkedinContainer {
  background: #2c3e79;
}

.devtoContainer {
  background: #fff;
  color: #000;
}

/* socialIcons */

.socialIcons {
  margin: 4em 1em 4em 1em;
  padding: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
}

ion-icon {
  color: grey;
  margin: 0 1em;
  font-size: 1em;
}

ion-icon:hover {
  cursor: pointer;
  color: #fff;
}

/* aboutSection */

.aboutSection {
  position: fixed;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: #000;
  padding: 2em 1em;
  transition: left 1.4s cubic-bezier(1, 0, 0, 1);
}

.closeIcon {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  font-family: sans-serif;
  color: #fff;
}

.closeIcon ion-icon {
  color: #fff;
  font-size: 1.5em;
}

.aboutHarnish {
  margin: 2em 0;
  padding: 2em;
  font-family: "Space Grotesk", sans-serif;
  display: flex;
  line-height: 2em;
}

.aboutHarnish a {
  color: red;
}

@media (max-width: 900px) {
  .aboutHarnish {
    flex-direction: column;
  }
}
